import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { UtilsService } from 'src/app/@core/utils.service';
import { PUBLISHER_TYPE } from 'src/app/@shared/enums/publisher-type';
import { UserPublicInfo } from 'src/app/networking/models/user-public-info.model';
import { Utils } from '../../../@global/utils';

@Component({
  selector: 'app-item-visit',
  templateUrl: './item-visit.component.html',
  styleUrls: ['./item-visit.component.scss'],
})
export class ItemVisitComponent implements OnInit, OnChanges {
  @Input() info: UserPublicInfo;
  @Input() response: any;
  @Output() cancelClick = new EventEmitter<string>();
  @Output() acceptClick = new EventEmitter<string>();
  public friendlyDate: string;

  public friendlyLocation = '';
  constructor(private utilsService: UtilsService) {}

  ngOnChanges(changes: SimpleChanges) {
    if (changes['info']?.currentValue) {
      this.friendlyLocation = Utils.generateUserFriendlyLocation(
        this.info.country,
        this.info.region,
        this.info.city,
        this.info.province,
        this.info.region_name
      );
      this.friendlyDate = Utils.getSpanishFormatDate(this.info.visit_date);
    }
  }

  ngOnInit() {}

  public onItemClick(userId) {
    this.utilsService.goProfile(userId, PUBLISHER_TYPE.USER);
  }
}
