import { Component, EventEmitter, Input, OnInit, Output, OnChanges, SimpleChanges } from '@angular/core';
import { ConnectionRequestModel } from '../../models/connection-request.model';
import { Utils } from '../../../@global/utils';

@Component({
  selector: 'app-item-request',
  templateUrl: './item-request.component.html',
  styleUrls: ['./item-request.component.scss'],
})
export class ItemRequestComponent implements OnInit, OnChanges {
  @Input() info: ConnectionRequestModel;
  @Output() cancelClick = new EventEmitter<string>();
  @Output() acceptClick = new EventEmitter<string>();
  @Output() actionItemClick = new EventEmitter<{ model: ConnectionRequestModel; isOrganization: boolean }>();
  public friendlyDate: string;
  public textResponse: string;

  constructor() {}

  ngOnInit() {
    console.log('on init');
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.info) {
      const info: ConnectionRequestModel = changes.info.currentValue;
      this.friendlyDate = Utils.getSpanishFormatDate(info.request_date);

      if (info.accept !== null && info.accept !== undefined) {
        this.textResponse = info.accept ? 'networking.accept' : 'networking.reject';
      }
    }
  }

  public onCancelClick(): void {
    this.cancelClick.emit(this.info.id);
  }

  public onAcceptClick(): void {
    this.acceptClick.emit(this.info.id);
  }

  public onItemClick(): void {
    this.actionItemClick.emit({
      model: this.info,
      isOrganization: false,
    });
  }
}
