import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { StorageService } from '../@core/storage.service';
import { Utils } from '../@global/utils';
import { CREATE_ORGANIZATION_FLOW_PAGES } from './create-organization-pages';
import { SetCanEnterCreatePageFlow } from '../@store/navigation/navigation.actions';
import { ProfileImageEditorBasePage } from '../base/profile-image-editor-base.page';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../@core/inform.service';

export class OrganizationBasePage extends ProfileImageEditorBasePage {
  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    public storage: StorageService
  ) {
    super(location, router, store, utilsService, camera, informService);
  }

  public async goNextFlowPage(pagePosition: number, navigationExtras?: any, createdPageId?: string) {
    if (Utils.isCreateOrganizationFlowLastPage(pagePosition)) {
      // if is last page
      this.store.dispatch(new SetCanEnterCreatePageFlow(false));
      this.router.navigate(['organization-profile', createdPageId]);
    } else {
      this.router.navigate([`/${CREATE_ORGANIZATION_FLOW_PAGES[pagePosition + 1]}`], navigationExtras);
    }
  }
}
