import { Component, OnInit } from '@angular/core';
import { Store } from '@ngxs/store';
import { ConnectionRequestModel } from '../models/connection-request.model';
import { BasePage } from '../../base/base.page';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { UtilsService } from '../../@core/utils.service';
import { NetworkState } from '../../@store/network/network.state';
import { Subscription } from 'rxjs';
import { NetworkingControllersService } from '../services/networking-controllers.service';

@Component({
  selector: 'app-my-requests',
  templateUrl: './my-requests.page.html',
  styleUrls: ['./my-requests.page.scss'],
})
export class MyRequestsPage extends BasePage implements OnInit {
  private pendingRequestsSubscription: Subscription;
  public items: ConnectionRequestModel[];

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    private networkingService: NetworkingControllersService
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {
    this.subscribeToPendingRequests();
  }

  ionViewWillEnter() {
    this.items = this.store.selectSnapshot(NetworkState.getPending);
  }

  public onResponseRequest(id, accept) {
    console.log('onResponseRequest ' + id + ' ' + accept);
    this.networkingService.onResponseRequest(id, accept);
  }

  private subscribeToPendingRequests(): void {
    this.pendingRequestsSubscription = this.store.select(NetworkState.getPending).subscribe((data) => {
      this.items = data;
    });
  }

  private disconnectObservers(): void {
    if (this.pendingRequestsSubscription) {
      this.pendingRequestsSubscription.unsubscribe();
    }
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }
}
