import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-complete-item',
  templateUrl: './complete-item.component.html',
  styleUrls: ['./complete-item.component.scss'],
})
export class CompleteItemComponent implements OnInit {
  @Input() info: any;
  @Input() editable: boolean = false;

  @Output() iconClick = new EventEmitter<string>();
  @Output() eraseClick = new EventEmitter<string>();

  constructor() {}

  ngOnInit() {}

  public onIconClick(): void {
    this.iconClick.emit(this.info.id);
  }

  public onEraseClick(id) {
    this.eraseClick.emit(id);
  }
}
