import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { StorageService } from '../@core/storage.service';
import { NavigationExtrasService } from '../@core/navigation-extras.service';
import { Constants } from '../@global/constants';
import { Utils } from '../@global/utils';
import { SIGNUP_FLOW_PAGES } from './signup-pages';
import { UtilsService } from '../@core/utils.service';
import { SignupInfoModel } from './signup-info.model';
import { SetUserInfo } from '../@store/user/user.actions';
import { take } from 'rxjs/operators';
import { GoogleAnalyticsService } from '../@core/google-analytics.service';
import { ProfileImageEditorBasePage } from '../base/profile-image-editor-base.page';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../@core/inform.service';

export class SignupBasePage extends ProfileImageEditorBasePage {
  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    public storage: StorageService,
    public navigationExtrasService: NavigationExtrasService,
    public googleAnalyticsService: GoogleAnalyticsService
  ) {
    super(location, router, store, utilsService, camera, informService);
  }

  public checkGoBack() {
    return this.navigationExtrasService.getExtras() && this.navigationExtrasService.getExtras().showGoBack;
  }

  public async updateSignupFlowInfo(pagePosition: number, dataName: string, dataValue: any) {
    await this.updateLastPage(pagePosition);
    await this.updateStorageDataObject(dataName, dataValue);
  }

  public async updateStorageDataObject(dataName: string, dataValue: any) {
    if (dataValue !== null) {
      const signupInfo = await this.storage.getSignupData();
      signupInfo[dataName] = dataValue;
      await this.storage.setSignUpData(signupInfo);
    }
  }

  public async updateLastPage(pagePosition: number) {
    await this.storage.setCompleteSignupPage(SIGNUP_FLOW_PAGES[pagePosition]);
  }

  public async goNextFlowPage(pagePosition: number, replaceUrl?: boolean) {
    if (Utils.isSignUpFlowLastPage(pagePosition)) {
      // if is last page
      this.router.navigate([Constants.FIRST_PAGE_AFTER_LOGIN], { replaceUrl });
    } else {
      this.router.navigate([`/${SIGNUP_FLOW_PAGES[pagePosition + 1]}`], { replaceUrl });
    }
  }

  public async finishSignupProcess(pagePosition: number) {
    const signupInfo = (await this.storage.getSignupData()) as SignupInfoModel;
    const userInfo = await this.utilsService.getUserRegisterDataToSave(signupInfo);
    this.store
      .dispatch(new SetUserInfo({ userInfo }))
      .pipe(take(1))
      .subscribe(
        async () => {
          await this.finishSignUpFlow();
          this.goNextFlowPage(pagePosition);
          this.googleAnalyticsService.userSignUp();
        },
        (error: any) => {
          console.log(error);
        }
      );
  }

  public async finishSignUpFlow() {
    await this.utilsService.removeSignUpDataFromLocalStorage();
    await this.storage.setAvoidOnboardingPages(true);
    const lang = await this.storage.getLanguage();
    await this.updateStorageDataObject('language', lang);
  }
}
